<template><undefined>
<h1>Notification 通知提醒框</h1>
<p>全局展示通知提醒信息。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>在系统四个角显示通知提醒信息。经常用于以下情况：</p>
<ul>
<li>较为复杂的通知内容。</li>
<li>带有交互的通知，给出用户下一步的行动点。</li>
<li>系统主动推送。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary\&quot; @click=\&quot;openNotification\&quot;&gt;\n        &lt;span&gt;打开通知提醒框&lt;/span&gt;\n    &lt;/button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        methods: {\n            openNotification() {\n                this.$notification.open({\n                  message: '这是标题',\n                  description: '这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案',\n                  onClose: close\n                });\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <button type="button" class="ant-btn ant-btn-primary" @click="openNotification">
        <span>打开通知提醒框</span>
    </button>
</template>

</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法，4.5 秒后自动关闭。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openNotification"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>打开通知提醒框<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>: {
            openNotification() {
                <span class="hljs-keyword">this</span>.$notification.open({
                  <span class="hljs-attr">message</span>: <span class="hljs-string">'这是标题'</span>,
                  <span class="hljs-attr">description</span>: <span class="hljs-string">'这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案'</span>,
                  <span class="hljs-attr">onClose</span>: close
                });
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
