    </button>
</template>

</div>
                  <div slot="description"><h4 id="zi-dong-guan-bi-de-yan-shi"><a class="header-anchor" href="#zi-dong-guan-bi-de-yan-shi" aria-hidden="true">¶</a> 自动关闭的延时</h4>
<p>自定义通知框自动关闭的延时，默认<code>4.5s</code>，取消自动关闭只要将该值设为 <code>0</code> 即可。<br>
可通过this.$notification.close(selfKey) 手动关闭通知框</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"openNotificationInfinite"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>打开通知提醒框<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ant-btn ant-btn-primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"closeNotice"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>关闭通知提醒框<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">methods</span>: {
            openNotificationInfinite() {
                <span class="hljs-keyword">this</span>.$notification.info({
                    <span class="hljs-attr">message</span>: <span class="hljs-string">'这是标题'</span>,
                    <span class="hljs-attr">description</span>: <span class="hljs-string">'我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭'</span>,
                    <span class="hljs-attr">duration</span>: <span class="hljs-number">0</span>,
                    <span class="hljs-attr">selfKey</span>:<span class="hljs-string">'noClose'</span>
                });
            },
            closeNotice(){
                <span class="hljs-keyword">this</span>.$notification.close(<span class="hljs-string">'noClose'</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
