<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-select style=\&quot;width: 120px;\&quot; :data=\&quot;options\&quot; @change=\&quot;onPlacementChange\&quot;&gt;&lt;/v-select&gt;\n    &lt;button type=\&quot;button\&quot; class=\&quot;ant-btn ant-btn-primary\&quot; @click=\&quot;openNotification\&quot;&gt;\n        &lt;span&gt;打开通知提醒框&lt;/span&gt;\n    &lt;/button&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    const options = [{\n        value: 'topLeft',\n        label: 'topLeft'\n    }, {\n        value: 'topRight',\n        label: 'topRight'\n    }, {\n        value: 'bottomLeft',\n        label: 'bottomLeft'\n    }, {\n        value: 'bottomRight',\n        label: 'bottomRight'\n    }];\n    export default {\n        data () {\n            return {\n                options\n            }\n        },\n        methods: {\n            openNotification() {\n                this.$notification.info({\n                  message: '这是标题',\n                  description: '这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案',\n                  onClose: close\n                });\n            },\n            onPlacementChange(val) {\n                this.$notification.config({\n                    placement: val,\n                });\n            },\n        }\n    }\n&quot;,&quot;style&quot;:null}">
