<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<ul>
<li><code v-pre="">this.$notification.success(config)</code></li>
<li><code v-pre="">this.$notification.error(config)</code></li>
<li><code v-pre="">this.$notification.info(config)</code></li>
<li><code v-pre="">this.$notification.warning(config)</code></li>
<li><code v-pre="">this.$notification.close(selfKey:String)</code></li>
</ul>
<h3 id="config-can-shu-ru-xia"><a class="header-anchor" href="#config-can-shu-ru-xia" aria-hidden="true">¶</a> config 参数如下：</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>message</td>
<td>通知提醒标题，必选</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>description</td>
<td>通知提醒内容，必选</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>selfKey</td>
<td>当前通知唯一标志</td>
<td>string</td>
<td>-</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭延时，设置为 null或者0 则不自动关闭,单位秒</td>
<td>number</td>
<td>4.5</td>
</tr>
<tr>
<td>placement</td>
<td>弹出位置，可选 <code v-pre="">topLeft</code> <code v-pre="">topRight</code> <code v-pre="">bottomLeft</code> <code v-pre="">bottomRight</code></td>
<td>string</td>
<td>topRight</td>
</tr>
<tr>
<td>onClose</td>
<td>关闭通知弹框时触发的回调函数(注：手动触发关闭不触发回调)</td>
<td>Function</td>
<td>-</td>
</tr>
</tbody>
</table>
<p>还提供了一个全局配置方法，在调用前提前配置，全局一次生效。</p>
<ul>
<li><code v-pre="">this.$notification.config(options)</code></li>
</ul>
<pre v-pre=""><code v-pre="" class="hljs language-js">    <span class="hljs-keyword">this</span>.$notification.config({
        <span class="hljs-attr">top</span>: <span class="hljs-number">100</span>,
        <span class="hljs-attr">duration</span>: <span class="hljs-number">3</span>,
    });
</code></pre>
<h3 id="options-can-shu-ru-xia"><a class="header-anchor" href="#options-can-shu-ru-xia" aria-hidden="true">¶</a> options 参数如下：</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placement</td>
<td>弹出位置，可选 <code v-pre="">topLeft</code> <code v-pre="">topRight</code> <code v-pre="">bottomLeft</code> <code v-pre="">bottomRight</code></td>
<td>string</td>
<td>topRight</td>
</tr>
<tr>
<td>top</td>
<td>消息从顶部弹出时，距离顶部的位置，单位像素。</td>
<td>number</td>
<td>24</td>
</tr>
<tr>
<td>bottom</td>
<td>消息从底部弹出时，距离底部的位置，单位像素。</td>
<td>number</td>
<td>24</td>
</tr>
<tr>
<td>duration</td>
<td>默认自动关闭延时，单位秒</td>
<td>number</td>
<td>4.5</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
    const options = [{
        value: 'topLeft',
        label: 'topLeft'
    }, {
        value: 'topRight',
        label: 'topRight'
    }, {
        value: 'bottomLeft',
        label: 'bottomLeft'
    }, {
        value: 'bottomRight',
        label: 'bottomRight'
    }];
    const close = () => {
        console.log('Notification was closed. Either the close button was clicked or duration time elapsed.');
    };
    export default {
        data () {
            return {
                options
            }
        },
        methods: {
            openNotification() {
                this.$notification.open({
                  message: '这是标题',
                  description: '这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案',
                  onClose: close
                });
            },

            openNotificationWithIcon(type) {
                this.$notification[type]({
                  message: '这是标题',
                  description: '这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案这是提示框的文案'
                });
            },
            openNotificationInfinite() {
                this.$notification.info({
                    message: '这是标题',
                    description: '我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭，我不会自动关闭',
                    duration: 0,
                    selfKey:'noClose'
                });
            },
            onPlacementChange(val) {
                this.$notification.config({
                    placement: val,
                });
            },
            closeNotice(){
                this.$notification.close('noClose');
            }
            
            
        }
    }
</script>


